/* The Modal (background) */
.modal {
    /* display: none; */
    position: absolute;
    z-index: 1;
    right: 0px;
    top: 0;
    width: 200px;
    height: 100%;
    overflow: auto;
    background-color: #111;
  }
  
  /* Modal Content */
  .modal-content {
    background-color: #111;
    margin: auto;
    padding: 20px;
    width: 80%;
    color: #fff;
  }

  .modal-content a {
    display: block;
    color: #ddd;
    font-size: 11px;
    text-decoration: none;
    width: 100%;
    margin-bottom: 7px;
    padding-bottom: 4px;
    border-bottom: 1px solid #666;
    height: 30px;
  }

  .modal-content a:hover {
    color: hotpink;
  }
  
  .del-btn {
    float: right;
    background: hotpink;
    border-radius: 40px;
    padding: 3px;
    color: #fff;
    border: 1px solid #eee;
}

  /* The Close Button */
  .close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }


/* width */
::-webkit-scrollbar {
width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
background: #555; 
}

